import React,{useState} from 'react';
import './App.css';

export default function App() {

  const [search, addSearch] = useState('')


  const name_list=['ayush', 'ram','Nigger', 'Idkwhatimtyping','ayushrawat','ayurveda']
  const search_item=search
  const list = name_list.filter(item=>{
    const fir = item.toLowerCase()
    const sec = search_item.toLowerCase()
    return fir.indexOf(sec) > -1
  })

  const dothis = (event) => {
    addSearch(event.target.value)
  }

  return (
  <div>
    <input onChange={dothis}/>
     {list.map(names=>{
      return <h1>{names}</h1>
     })}
  </div>
  )
} 


